html {
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 30px 40px;
  display: grid;
}

@media (width >= 500px) {
  .people {
    grid-template-columns: repeat(2, 200px);
    left: 35px;
    right: 35px;
  }
}

@media (width >= 750px) {
  .people {
    grid-template-columns: repeat(2, 200px);
    left: 160px;
    right: 160px;
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
    left: 55px;
    right: 55px;
  }
}

@media (width >= 1500px) {
  .people {
    grid-template-columns: repeat(4, 200px);
    left: 305px;
    right: 305px;
  }
}

.people__person {
  box-sizing: border-box;
  flex-direction: column;
  padding: 0;
  transition: transform 1s;
  display: flex;
  top: 30px;
}

.people__person:hover {
  background-color: #eee;
  width: 220px;
  height: auto;
  transition: all 1s;
  transform: scale(1.2);
}

.people__person:hover .person__photo {
  border-radius: 60px;
  width: 220px;
  height: 220px;
}

.people__person--extra {
  color: #000;
  text-align: center;
  opacity: 0;
  margin-top: 10px;
  margin-bottom: 19px;
  font-size: 18px;
  transition: opacity .3s;
}

.people__person:hover .people__person--extra {
  opacity: 1;
}

.people__person--photo {
  display: inline-block;
  position: relative;
}

.person__photo {
  background-position: center;
  border-radius: 200px;
  width: 200px;
  height: 200px;
  display: block;
}

.person__rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  padding-top: 7px;
  padding-bottom: 7px;
  font-size: 16px;
  line-height: 18.4px;
  display: flex;
  position: absolute;
}

.person__name {
  text-align: center;
  color: #2f4f4f;
  white-space: nowrap;
  font-size: 20px;
  line-height: 23px;
}
